import React from "react";
import { iconList } from "./myIcons";
import {
  Typography,
  Grid,
  makeStyles,
  Button,
  Container,
} from "@material-ui/core";
import "./svgStyle.css";

const useStyles = makeStyles((theme) => ({
  about: {
    display: "flex",
    paddingTop: theme.spacing(3),
    paddingBottom: theme.spacing(3),
    height: "50%",
  },
  iconContainer: {
    margin: "1vh",
    display: "flex",
    flexDirection: "column",
    alignItems: "center",
  },
  iconStyle: {
    margin: "0.1em",
  },
}));

const About = () => {
  const classes = useStyles();
  return (
    <Grid style={{ height: "100vh", backgroundColor: "#242424" }}>
      <Container className={classes.about}>SOMTHING ABOUT ME</Container>
      <Container className={classes.about}>
        <Grid container direction="column">
          <Grid
            container
            justify="center"
            style={{ width: "100%", marginBottom: "1em" }}
          >
            <Typography
              variant="h4"
              style={{
                fontFamily: "Anton",
                color: "#FBD630",
                letterSpacing: "0.1em",
              }}
            >
              MY SKILLS
            </Typography>
          </Grid>
          <Grid
            container
            justify="space-around"
            style={{ paddingRight: "1em", paddingLeft: "1em" }}
          >
            {iconList.map((icon) => (
              <Grid key={icon.name} className={classes.iconContainer}>
                <Grid item className={classes.iconStyle}>
                  <icon.component />
                </Grid>
                <Grid item>
                  <Typography style={{ color: "#efefef", fontSize: "2vh" }}>
                    {icon.name}
                  </Typography>
                </Grid>
              </Grid>
            ))}
          </Grid>
        </Grid>
      </Container>
    </Grid>
  );
};

export default About;
